.app {
    padding: 40px 15px;
}

.events_text-input {
    position: relative;
    max-width: 468px;
    margin-top: 40px 0;
    display: flex;
    justify-content: space-between;
    border: 1.2px solid #6d6d6d;
    background-color: #fff;
    border-radius: 38px;
    align-items: center;
    overflow: hidden;
    padding: 5px;
    margin: 0 auto;
}

.events_text-input input[type="text"] {
    border: none;
    flex: auto;
    background-color: rgba(0, 0, 0, 0);
    color: #6d6d6d;
    line-height: 22px;
    font-weight: 300;
    font-size: 18px;
    padding: .5rem .5rem .5rem 3rem;
}

.events_text-input input[type="reset"] {
    color: #000;
    background-color: #fff;
    border: none;
    border-left: none;
    position: absolute;
    top: 10px;
    right: 65px;
    z-index: 3;
    font-size: 24px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    padding: 0;
    opacity: 0;
}

.events_text-input:focus-within input[type=reset] {
    opacity: 1;
}

.events_text-input input[type="button"] {
    background-color: #0096d6;
    color: #fff;
    border: none;
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    padding: .5rem 1rem;
    border-radius: 38px;
}

.events_text-input svg {
    left: 13px;
    top: 9px;
    font-size: 1.5rem;
    display: inline-table;
    position: absolute;
}

.events_text-input input:focus {
    outline: none;
}

.events_event-type {
    max-width: 468px;
    display: flex;
    justify-content: center;
    margin: 20px auto;
    padding: 5px 0;
    border-radius: 4px;
    border: 2px solid #009de0;
}

.events_event-type label {
    border-right: 0;
    padding: 7px 0;
    cursor: pointer;
    display: block;
    width: 100%;
    text-align: center;
    transition: background-color .2s ease;
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 4px;
    background-color: #fff;
    color: #009de0;
}

.events_event-type input:checked + label {
    background-color: #009de0;
    color: #fff;
}

.radio-group_item {
    flex-grow: 1;
    flex-basis: 0;
    margin: 0 5px;
}

.events_event-type form {
    display: flex;
    width: 100%;
}

.events_event-type input {
    position: absolute;
    left: -9999px;
}

.events_tag-filter {
    text-align: center;
    margin-top: 30px;
}

.events_tag-filter h3 {
    text-align: left;
}

.events_tag-filter input[type="button"] {
    background-color: #0096d6;
    color: #fff;
    border: none;
    font-size: 16px;
    line-height: 24px;
    padding: .125rem 1rem;
    border-radius: 38px;
    margin-bottom: 10px;
}

.tag-filters {
    display: flex;
    height: 240px;
    overflow-y: scroll;
    padding: 20px;
    gap: 10px;
    flex-direction: column;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0px 4px 10px 0px #cbcbcb;
    border: 2px solid #0096d6;
}

.tag-filters_tags {
    padding: 0 0 .25rem;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 9px 1px;
}

.tag-filters_tag label {
    display: flex;
    margin: 2px 10px 2px 0;
    text-transform: capitalize;
    position: relative;
    background-color: #f0f0f0;
    padding: 4px 12px 4px 8px;
    cursor: pointer;
    border-radius: 14px;
    line-height: 18px;
}

.c-event-list__listing ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: center;
    padding: 24px 0 1rem 0;
    gap: 20px;
}

.c-event-list__listing ul li {
    padding: 15px;
    border-radius: 8px;
    border: 1px solid #acacac;
    overflow: hidden;
    transition: .3s ease;
    width: calc(50% - 61px);
}

.c-event-list__listing ul li:hover {
    box-shadow: 0 0 8px 0px #acacac;
}

.c-event-list__listing ul li h2 {
    margin-bottom: 10px;
}

.c-event-list__listing ul li h3 {
    border-radius: 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-transform: lowercase;
    background-color: #ababab;
    color: #fff;
    display: inline-block;
    padding: 3px 10px;
    margin: 0;
}

.c-event-list__listing ul li a {
    color: #009de0;
    text-decoration: none;
}

.c-event-list__listing ul li a:hover {
    color: #037db0;
}

.pagination-navigation {
    text-align: center;
}
